import React, { useEffect } from "react";
import { Container, Typography } from "@material-ui/core";


const About = () => {
  useEffect(() => {
    document.title = "About US";
  }, []);

  return (
    <Container maxWidth="md" >
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 10, marginTop: 80 }} color="primary">
        About Mission Organic Mizoram
          </Typography>

      <Typography gutterBottom style={{ marginBottom: 10 }}>
        Realizing the potential and benefits of organic farming and to improve the economic condition of farmers in the country, Government of India is promoting organic farming through the dedicated scheme called Mission Organic Value Chain Development for North Eastern Region (MOVCDNER) under National Mission for Sustainable Agriculture (NMSA) since 2015-16.
          </Typography>
      <Typography gutterBottom style={{ marginBottom: 10 }}>
        Under MOVCDNER, assistance is provided to the farmers in a value chain mode starting from formation of Farmers Producer Organizations (FPOs), on/off farm input production, supply of seeds/ planting materials, post-harvest infrastructure including collection, sorting, grading facilities,   establishment of integrated processing unit, refrigerated transportation, pre-cooling/ cold storage chamber, branding, labeling and packaging, etc.
          </Typography>
      <Typography gutterBottom >
        This scheme is implemented through State Governments at district and village level depending on the interest of the farmers. MOVCDNER scheme is implemented in the States of Arunachal Pradesh, Assam, Manipur, Meghalaya, Mizoram, Nagaland, Sikkim and Tripura since 2015-16.
          </Typography>
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 10, marginTop: 20 }} color="primary">
        About MOVCDNER
          </Typography>

      <Typography gutterBottom style={{ marginBottom: 10 }}>
        Mission Organic Value Chain Development for North East Region (MOVCD-NER) is a Central Sector Scheme, a sub-mission under National Mission for Sustainable Agriculture (NMSA), launched by the Ministry of Agriculture and Farmers Welfare for implementation in the states of Arunachal Pradesh, Assam, Manipur, Meghalaya, Mizoram, Nagaland, Sikkim and Tripura, during the 12th plan period. The scheme aims to develop certified organic production in a value chain mode to link growers with consumers and to support the development of the entire value chain starting from inputs, seeds, certification, to the creation of facilities for collection, aggregation, processing, marketing and brand building initiative.
            </Typography>
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 10, marginTop: 20 }} color="primary">
        About FPO
          </Typography>

      <Typography gutterBottom style={{ marginBottom: 10 }}>
        FPOs/FPCs are a federation of Farmers Interest Groups (FIGs) created at village level. These FIGs are created based on development of commodity specific clusters for ease in training, handholding, certification and collection and aggregation. FIGs at village level have the farmers organized into  groups and linked together into organisations/companies at District/State level in accordance with SFAC guidelines for forming Farmer Producer Companies.
            </Typography>
      <Typography variant="h5" gutterBottom style={{ fontWeight: 700, marginBottom: 10, marginTop: 20 }} color="primary">
        Main Crop
          </Typography>

      <Typography gutterBottom style={{ marginBottom: 10 }}>
        <b>Turmeric:</b> Among the spices crop, turmeric being a short gestation crop, is an ideal crop for the poor farmers and doing well in Mizoram. The variety that has been produced in Mizoram are RCT-I, local variety and along with other popular varieties such as Lakadong.<br />
Sowing time - March to May<br />
Harvesting period - January

            </Typography>
      <Typography gutterBottom style={{ marginBottom: 10 }}>
        <b>Ginger:</b> As an indispensable culinary spice, ginger is cultivated in many parts of Mizoram and has been a good source of income for farmers. Harvested ginger is sold not only within the state but also outside of it. <br />
Sowing time - March to May<br />
Harvesting period - January to March


            </Typography>
      <Typography>
        <b>Bird's Eye Chilli:</b> One of the hottest chilli in the world, it is small in size and green in colour which turns red on ripening. They are eaten as chutney, pickled, or as roasted dried and ground flakes. As the demand is high, the market is good and has been a good source of income for the farmers.<br />
Sowing time- March to May<br />
Harvesting period- November to February


            </Typography>

    </Container>
  );
};

export default About;
